.search {
	display: flex;
	align-items: center;
	position: relative;
	flex: 1;
}

.search_input {
	width: 100%;
	border-radius: 22px;
	border: 1px solid transparent;
	outline: none;
	padding: 13px 60px 13px 20px;
	color: #000;
	font-size: 16px;
	line-height: 20px;
	caret-color: #FEBA0C;
}
.search_input:focus {
	background: #FFFFFF;
	border: 1px solid #fa5d15;
}

.search_input::placeholder {
	color: #afa7a6;
}

.search_btn {
	background-color: transparent;
	outline: none;
	border: none;
	padding: 0;
	cursor: pointer;
	position: absolute;
	right: 20px;
	display: flex;
}
.search_btn svg {
	fill: #7B8E98;
}
.search_btn_disabled svg {
	fill: #CFD8DC;
}
.search_btn:focus svg {
	fill: #fe3c0c;
}
.search_btn:hover svg {
	fill:  #fe3c0c;
}

@media (max-width: 350px) {
	.search__input {
		max-width: 400px;
	}
}
