.header { 
    width: 100%;
    background-color: #feba0c;
    padding: 15px 0px;

}

.header_wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 80px;

}

@media ( max-width: 700px ) {
    .header_wrapper {
        flex-direction:column;
        align-items: stretch;
        gap: 20px; 

    }
}


.iconsMenu {
	display: flex;
	gap: 30px;
	flex-wrap: wrap;
}

.favoritesLink {
	position: relative;
	width: 31px;
	height: 31px;
	
}
.iconsMenu svg {
	width: 100%;
	height: 100%;
}


.iconBubble {
	background-color: #11ff00;
	border-radius: 50%;
	color: #000000;
	border: 2px solid #feba0c;
	font-size: 14px;
	width: 22px;
	height: 22px;
	line-height: 1.35;
	display: flex;
	align-items: center;
	justify-content: center;
	box-sizing: border-box;
	position: absolute;
	bottom: 65%;
	left: 70%;
}

.iconsMenuItem {
	display: flex;
	font-size: 18px;
	font-weight: 600;
	align-items: center;
	gap: 10px;
	text-decoration: none;
	color: #1a1a1a;
}

.iconsMenuItem svg {
	width: 28px;
	height: 28px;
	flex-shrink: 0;
	fill: currentColor;
}
 