.banner {
    width: 100%;
    background-color: #feba0c;
    padding-top: 70px;
    padding-bottom: 200px;
    clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 83%);
  }

  .banner_container {
    display: flex;
  }

  .lefttt {
    max-width: 425px;
  }

  .righttt {
    flex: 1;
    margin-left:35px;
  }

  .title {
    font-size: 48px;
    line-height: 52px;
    font-weight: 900;
    margin-bottom: 20px;
  }
  
  .subtitleee {
    display: block;
    font-size: 24px;
    line-height: 28px;
    font-weight: 300;
    margin-bottom: 40px;
  }

  img {
    max-width: 100%;
  }

  .link_btn {
    padding: 16px 28px;
    padding-right: 48px;
    background-color: #ffff;
    border-radius: 60px;
    display: flex;
    text-decoration: none;
    display: flex;
    align-items: center;
    font-size: 20px;
    line-height: 28px;
    font-weight: 800;
    width: min-content;
    box-shadow: 0px 0px 1px rgba(40, 41, 61, 0.02),
      0px 2px 16px rgba(96, 97, 112, 0.16);
    gap: 5px;
  }

  .link_btn:hover {
    box-shadow: 0px 14px 28px rgba(0, 0, 0, 0.25), 0px 10px 10px rgba(0, 0, 0, 0.22);
    background-color: #54c3ff;
    background-position: 100% 0;
    color:#fff;
  }
  
  a.link_btn {
    color: #54c3ff;
  }